body .slick-prev:before,
body .slick-next:before {
  font-family: "slick";
  font-size: 20px;
  line-height: 1;

  opacity: 0.75;
  color: black;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*:focus {
  outline: 0;
  outline: none;
}

.slick-prev {
  left: -40px !important;
}

.slick-next {
  right: -40px !important;
}

.slick-dots {
  bottom: -35px !important;
}
